import React from 'react'
import pubs from './bibly.js'

export default class Publist extends React.Component {
  constructor() {
    super();
    const publist = pubs.map((pub) =>
        <div>
          <div className = "left-no-pad">
            <a href={pub.linky}>PDF</a>
          </div>
          <div className = "left-tight">
            <a href=''>bibtex</a>
          </div>

          <div className = "right">
            <div className = "right-marge">
              <strong>{pub.title}</strong>
              <br/>
              {pub.authors}
              <br/>
              <span className="italic-span"> {pub.rest} </span>
            </div>
          </div>
        </div>
      );
    this.state = {'publist':publist};
    console.log(this.state)
  }

  render() {
    const publist = this.state.publist;
    return (
      <div>
      // {publist}
      </div>
    );
  }
}



